import instance from "../../../../api/index.js"
export default {
	data() {
		return {
			checked: true,
			biaoqian:'1',
			bz:'',
			token:'',
			carlist:'',//数据
			dizhi:'',
			yhmoney:'',//优惠金额
			yf:'',//运费
			count:''
		};
	},
	onLoad(options) {
		console.log(options.list)
		this.token=uni.getStorageSync("token");
		instance({
			url:'/api/shopCar/shopCarSettle',
			data:{
				token:this.token,
				shopCarNos:options.list
			},
			method:'POST',
				header:{
				   'content-type':'application/json',
			}
		}).then(res=>{
			this.carlist=res.data.data[0].productList//商品信息
			this.count=res.data.data[0].productList.length//商品数量
			this.yf=res.data.data[0].logisticsFee/100//运费
			let yharr=this.carlist.map(item=>item.useCoupon);
			let yhmoney=yharr.reduce(function(prev,cur){
					return prev + cur
			})
			this.yhmoney=yhmoney//优惠金额
		})
	},
	methods: {
		radioChange() {
			this.checked = !this.checked;
		},
		changewx(e){
			this.biaoqian=e
		}
	},
	computed:{
		totalPrice() {
			let totalPrice = 0;
			this.carlist.map(item => {
				totalPrice += item.shopCarNum * item.agentPrice
			})
			return totalPrice
		},
		real(){
			let realmoney=0;
			if(this.checked==true){
				 realmoney=this.totalPrice-this.yhmoney
			}
			if(this.checked==false){
				 realmoney=this.totalPrice
			}
			return realmoney
		}
			
	}
	
};
